.menu{
	font-weight: 500;
	font-size: 5vw;

	&__item{
		position: relative;
		margin: 0 0 8vw;
	}
	&__link{
		position: relative;
		display: inline-block;
		text-align: center;

		&.active .v,
		&:hover .v{
			font-weight: 700;
		}

		.v{
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		.h{
			display: block;
			opacity: 0;
			font-weight: 700;
		}
	}
}

@media (min-width: @w-xxs) {
	.menu{}
}


@media (min-width: @w-xs) {
	.menu{}
}


@media (min-width: @w-sm) {
	.menu{}
}


@media (min-width: @w-md) {
	.menu{}
}


@media (min-width: @w-lg) {
	.menu{
		margin: 0 -.9vw;
		display: flex;
		justify-content: space-between;
		font-size: 1.25vw;

		&__item{
			margin: 0 .9vw;

			&:not(:last-child){
				&::after{
					content:'';
					display: inline-block;
					width: .5em;
					height: 0.5em;
					border: 1px solid #968CFF;
					box-sizing: border-box;
					transform: rotate(45deg);
					margin-left: 1.46vw;
				}
			}
		}
	}
}