@keyframes rotate {
  100%{
    transform: rotate(360deg);
  }
}
@keyframes rotate-planet {
  100%{
    transform: translate(32%, 60%) rotate(360deg);
  }
}

@keyframes show-icons {
  5%, 30%{
    opacity: 1;
  }
  0%, 36%, 100%{
    opacity: 0;
  }
}
