@keyframes move-btn-arrow {
  50%{
    transform: translate(20%, 0);
  }
}

@keyframes move-arrows-1 {
  0%, 40%, 100%{
    transform: translate(-1%, 1%);
  }
  20%{
    transform: translate(4%, -4%);
  }
}

@keyframes move-arrow-vertical {
  0%, 50%, 100%{
    transform: translate(-50%, 0%) ;
  }
  25%{
    transform: translate(-50%, 25%) ;
  }
  75%{
    transform: translate(-50%, -25%) ;
  }
}

