@keyframes transfusion-light-1 {
  0%, 5%, 95%, 100%{
    opacity: 1;
  }
  45%, 55%{
    opacity: 0;
  }
}
@keyframes transfusion-light-2 {
  0%, 5%, 95%, 100%{
    opacity: 0;
  }
  45%, 55%{
    opacity: 1;
  }
}
@keyframes showWithDelay {
  100%{
    opacity: .6;
  }
}
@keyframes pulseOpacity {
  50%{
    opacity: 0;
  }
}
@keyframes pulse {
  50%{
    transform: scale(1.05);
  }
}
@keyframes pulse2 {
  100%{
    transform: scale(1.05);
  }
}

@keyframes fly-h {
  0%, 100%{
    transform: translate(0%, 0%);
  }
  10%{
    transform: translate(2%, 4%);
  }
  20%{
    transform: translate(-2%, 4%);
  }
  30%{
    transform: translate(2%, -4%);
  }
  40%{
    transform: translate(-2%, -4%);
  }
  50%{
    transform: translate(2%, 4%);
  }
  60%{
    transform: translate(2%, -4%);
  }
  70%{
    transform: translate(-2%, 4%);
  }
  80%{
    transform: translate(-2%, -4%);
  }
  90%{
    transform: translate(2%, -4%);
  }
}
@keyframes fly-h-2 {
  0%, 100%{
    transform: translate(0%, 0%);
  }
  10%{
    transform: translate(1%, 1%);
  }
  20%{
    transform: translate(-1%, 1%);
  }
  30%{
    transform: translate(1%, -1%);
  }
  40%{
    transform: translate(-1%, -1%);
  }
  50%{
    transform: translate(1%, 1%);
  }
  60%{
    transform: translate(1%, -1%);
  }
  70%{
    transform: translate(-1%, 1%);
  }
  80%{
    transform: translate(-1%, -1%);
  }
  90%{
    transform: translate(1%, -1%);
  }
}
@keyframes fly-h-3 {
  0%, 100%{
    transform: translate(0%, 0%);
  }
  10%{
    transform: translate(0.5%, 0.5%);
  }
  20%{
    transform: translate(-0.5%, 0.5%);
  }
  30%{
    transform: translate(0.5%, -0.5%);
  }
  40%{
    transform: translate(-0.5%, -0.5%);
  }
  50%{
    transform: translate(0.5%, 0.5%);
  }
  60%{
    transform: translate(0.5%, -0.5%);
  }
  70%{
    transform: translate(-0.5%, 0.5%);
  }
  80%{
    transform: translate(-0.5%, -0.5%);
  }
  90%{
    transform: translate(0.5%, -0.5%);
  }
}
@keyframes fly-to-right {
  0%, 100%{
    transform: translate(0%, 0%);
  }
  10%{
    transform: translate(1%, 1%);
  }
  20%{
    transform: translate(0%, 1%);
  }
  30%{
    transform: translate(1%, 0%);
  }
  40%{
    transform: translate(0%, 0%);
  }
  50%{
    transform: translate(1%, 1%);
  }
  60%{
    transform: translate(1%, 0%);
  }
  70%{
    transform: translate(0%, 1%);
  }
  80%{
    transform: translate(0%, 0%);
  }
  90%{
    transform: translate(1%, 0%);
  }
}
@keyframes fly-to-right-2 {
  0%, 100%{
    transform: translate(0%, 0%);
  }
  10%{
    transform: translate(1%, -1%);
  }
  20%{
    transform: translate(0%, -1%);
  }
  30%{
    transform: translate(1%, 0%);
  }
  40%{
    transform: translate(0%, 0%);
  }
  50%{
    transform: translate(1%, -1%);
  }
  60%{
    transform: translate(1%, 0%);
  }
  70%{
    transform: translate(0%, -1%);
  }
  80%{
    transform: translate(0%, 0%);
  }
  90%{
    transform: translate(1%, 0%);
  }
}

@keyframes move-left-right {
  0%, 50%, 100%{
    transform: translate(0%, 0%);
  }
  25%{
    transform: translate(-2%, 0%);
  }
  75%{
    transform: translate(2%, 0%);
  }
}

@keyframes move-up-down {
  50%{
    transform: translate(0%, 50%);
  }
}

@keyframes rotate-ellipse{
  50%{
    transform: rotate(3deg) translate(3%, 3%);
  }
}

@keyframes vertical-hide-icon{
  0%{
    transform: translate(0%, 110%);
  }
  15%, 85%{
    transform: translate(0%, 0%);
  }
  100%{
    transform: translate(0%, -110%);
  }
}

@keyframes vertical-hide-icon-2{
  0%{
    transform: translate(0%, 65%);
  }
  // 30%, 70%{
  //   transform: translate(0%, 0%);
  // }
  100%{
    transform: translate(0%, -65%);
  }
}

@keyframes marque {
  0%{
    transform: translateX(-25%);
  }
  100%{
    transform: translateX(-50%);
  }
}
