/*  ШАПКА  */
.header{
  position: relative;
  z-index: 10;
  padding: 6vw 0;
  
  &__row{
    margin: 0 -4vw;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__toggle{
    width: 7.2vw;
    margin: 0 4vw;
    ordeR: 3;
  }
  &__logo{
    width: 44vw;
    margin: 0 4vw;
    order: 1;
  }
  &__btns{
    margin: 0 4vw;
    width: 22vw;
    order: 5;
  }
  &__nav{
    display: none;
  }
}

@media (min-width: @w-xxs) {
  .header{}
}


@media (min-width: @w-xs) {
  .header{}
}


@media (min-width: @w-sm) {
  .header{}
}


@media (min-width: @w-md) {
  .header{}
}


@media (min-width: @w-lg) {
  .header{
    padding: 4.8vh 0;

    &__row{
      margin: 0;
    }
    &__logo{
      width: 18.75vw;
      margin: 0;
    }
    &__btns{
      margin: 0;
      width: 10.16vw;
    }
    &__nav{
      display: block;
      margin: 0 6.9vw 0 auto;
      order: 3;
      // width: 26vw;
    }
    &__toggle{
      display: none;
    }
  }
}