.toggle{
  display: block;
  width: 7.2vw;
  padding: 0;
  background: transparent;
  border: 0px;
  text-align: center;
  font-size: 1px;
  cursor: pointer;
  outline: none;

  i{
    display: block;
    background: var(--color-blue);
    height: 2px;
    border-radius: .5vw;

    &:nth-child(2){
      margin: 2vw 0 2vw auto;
      width: 70%;
    }
  }
}

