/*  БАЗОВЫЕ НАСТРОЙКИ  */
.reset();
html{
  font-size: 14px;
  scroll-behavior:smooth;
}
body{
  font-family: var(--font-noah);
  line-height: 1.3;
  font-size: 3.33vw;
  color: @color1;
  font-weight: 400;
  font-style: normal;
  background: #F1F6F5;
  color: #000;
  
  .lg-min({
    font-size: 0.83vw;
  });
}
*{
  box-sizing: border-box;
  max-height: 100000px;
}
a{
  color: inherit;
  outline: none;
  text-decoration: none;
}
button, [role="button"]{
  cursor: pointer;
}
strong{
	font-weight: 700;
}



.main{
  min-height: 100vh;
  min-width: 360px;
  overflow: hidden;
}
.container{
  position: relative;
  z-index: 1;
  margin: 0 4.44vw;

  .lg-min({
    margin: 0 5.2vw;
  });
}
.ta-c{
  text-align: center;
}
.d-ib{
  display: inline-block;
}
.hidden{
  display: none !important;
}
.position-hide{
  position: absolute;
  left: -9999px;
  width: 1px;
}
.overflow-hidden{
  overflow: hidden;
}
.img-block{
  width: 100%;
  display: block;
  height: auto;
}
.opacity-0{
  opacity: 0;
}

