.btn1{
  display: inline-block;
  position: relative;

  font-family: var(--font-noah);
  font-weight: 700;
  font-size: 1.2em;
  color: #000;
  line-height: 1.1;

  &::after{
    content: '';
    display: block;
    position: absolute;
    z-index: 1;
    width: 100%;
    left: 0;
    top: 0;
    height: 100%;
    background: var(--color-btn-h, #00E1CA);
    z-index: 0;
    pointer-events: none;
    transform-origin: 100% 100%;
    // transform: scale(1.03, 1.13);
    transform: translate(4%, 12%) scale(1.03, 1.13);
    border-radius: 2vmin;
    transition: 0.3s transform;

    .lg-min({
      border-radius: 1.04vw;
    });
  }
  &>*{
    position: relative;
    z-index: 3;
  }

  &:hover{
    .btn1__wrap{
      transform: translate(4%, 12%)
    }
    &::after{
      transform: translate(4%, 12%) scale(1, 1);
    }
  }

  &&_blue{
    --color-btn: var(--color-blue);
    --color-btn-h: conic-gradient(from -90deg at 93% 72%, #FFFFFD 0deg, #00B6A3 360deg);
    
    &:hover .btn1__wrap{
      background: #02D2BD;
    }
  }
  &&_yellow{
    --color-btn: var(--color-yellow);
    --color-btn-h: conic-gradient(from -90deg at 93% 72%, #FFFFFD 0deg, #E0BA14 360deg);

    &:hover .btn1__wrap{
      background: #ECC413;
    }
  }

  &__wrap{
    display: inline-flex;
    align-items: center;
    border-radius: 2vmin;
    transition: .3s background;
    padding: 1.2em 1.8em 1.1em;
    background: var(--color-btn, #00E1CA);
    transition: .3s transform, .3s background;
    transform-origin: 0 0;

    .lg-min({
      font-size: 1.56vw;
      padding: 1.2em 2.235em 1.1em;
      border-radius: 1.04vw;
    });
  }
  
  &__arrow-r{
    display: inline-block;
    margin-left: 1em;
    width: 1.83em;
    height: .83em;
    background: url(../../assets/img/svg/btn-arrow-1.svg) center / contain no-repeat;
    animation: 2s move-btn-arrow linear infinite;
  }
}

.btn2{
  display: inline-block;
  min-width: 22vw;
  padding: .743em 1em;
  border: 1px solid var(--color-pink);

  line-height: 1.1;
  font-weight: 700;
  color: #000;
  transition: .3s color, .3s background;
  border-radius: .58em;
  text-align: center;
  font-family: var(--font-noah);

  .lg-min({
    min-width: 10.16vw;
    border-width: 2px;
    font-size: 1.25vw;
  });

  &:hover{
    color: #fff;
    background: var(--color-pink);
    font-weight: 700;
  }
}

