.top{
  position: relative;
  padding: 0 0 9vw;
  background: url(../../../../assets/img/top/bg1.jpg) center / cover no-repeat;
  height: 100vh;
  overflow: hidden;

  &__wrap{
    position: absolute;
    z-index: 5;
    width: 100%;
    height: 50%;
    top: 0;
    left: 0;
  }
  &__word{
  	display: flex;
    justify-content: center;
    position: absolute;
    z-index: 5;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -57%);
    width: calc(100% + .12em);
    line-height: 1;
    text-align: center;
    font-family: var(--font-neueeinstellung);
    font-weight: 500;
    font-size: 26.82vw;
    letter-spacing: -.05em;
    margin-left: -.06em;
    overflow: hidden;
    height: 1em;
  }
  &__word-in{
    display: block;
    margin-top: -.15em;
  }
  &__cube{
    position: absolute;
    z-index: 6;
    top: 50%;
    left: 50%;
    width: 85.35vw;
    // transform: translate(-64.4%, -16.6%);
    margin-top: -11.7%;
    margin-left: -54%;
    
    svg{
      display: block;
      width: 100%;
      height: auto;
      overflow: visible;
      pointer-events: none;
    }

    .a, .b, .c{
      opacity: 0;
    }

    &[data-type="a"] .a{
      opacity: 1;
    }
    &[data-type="b"] .b{
      opacity: 1;
    }
    &[data-type="c"] .c{
      opacity: 1;
    }
  }

  &__bottom{
    position: absolute;
    z-index: 20;
    bottom: 10vw;
    left: 0;
    width: 100%;

    &-row{
      // display: flex;
      // align-items: flex-end;
      // justify-content: space-between;
    }
  }
  &__desc{
    text-align: right;
    font-family: var(--font-neueeinstellung);
  }
  &__txt-1{
    width: 40vw;
    margin: 0 0 4vw auto;
  }
  &__title{
    display: block;
    margin: 0 0 8vw auto ;
    font-weight: 500;
    line-height: 1;
    font-size: 1.7em;
  }
  &__btns{
    display: flex;
    justify-content: flex-end;

    &-2{
      margin-left: 6vw;
    }
  }

  &__dots{
    // margin-bottom: 6vmin;
    margin-bottom: -8vw;
  }
  &__dot{
    position: relative;
    width: max-content;
    display: flex;
    align-items: center;
    margin-bottom: 3vmin;
    font-family: var(--font-neueeinstellung);
    font-weight: 400;
    color: #000;
    font-size: 1.1em;
    transition: .3s color;
    padding-right: 1.1em;
    user-select: none;

    &:last-child{
      margin-bottom: 0;
    }

    &:hover{
      font-weight: 700;
      color: var(--btn-color);
    }

    &::after{
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      border-radius: 1em;
      border: 2px solid var(--btn-color);
      box-sizing: border-box;
      opacity: 0;
      transition: .3s opacity;
    }

    &.active{
      font-weight: 700;

      &::after{
        opacity: 1;
      }
      &>i{
        border-color: transparent;
        transform: translate(.5em, 0);
      }
    }

    &>i{
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      z-index: 1;
      width: 2em;
      height: 2em;
      border: 2px solid var(--btn-color);
      border-radius: 50%;
      margin-right: .7em;
      transition: .3s border-color, .3s transform;

      svg{
        display: block;
        width: 70%;
        height: auto;
        fill: var(--btn-color);
      }
    }
    &>span{
      display: block;
      position: relative;
      z-index: 1;
      transform: translate(0, -.1em);
    }

    &&_blue{
      --btn-color: var(--color-blue);
    }
    &&_yellow{
      --btn-color: var(--color-yellow);
    }
    &&_pink{
      --btn-color: var(--color-pink);
    }
  }

  &__decor{
    position: absolute;
    z-index: 90;

    &&-1{
      width: 13.65vw;
      top: -11.2%;
      left: 36%;
    }
    &&-2{
      width: 7.24vw;
      top: 15.5%;
      right: -3.9%;
    }
    &&-3{
      width: 7.55vw;
      top: 6.2%;
      left: 26.9%;
    }
    &&-4{
      width: 12.8vw;
      top: 12.3%;
      left: 11.9%;
    }
    &&-5{
      width: 11.98vw;
      top: 50.8%;
      right: -0.5%;
    }
    &&-6{
      width: 8.54vw;
      bottom: -6.3%;
      left: 22.8%;
    }
    &&-7{
      width: 17.5vw;
      bottom: -22.3%;
      left: 34.8%;
    }
  }

  .act{
    opacity: 0;
  }
  .def,
  .act{
    transition: .3s;
  }
}


@media (min-width: @w-xxs) {
  .top{}
}


@media (min-width: @w-xs) {
  .top{}
}


@media (min-width: @w-sm) {
  .top{}
}


@media (min-width: @w-md) {
  .top{}
}


@media (min-width: @w-lg) {
  .top{

    &__wrap{
      height: 100%;
    }
    &__cube{
      width: 41.35vw;
      // transform: translate(-91.4%, -41.6%);
      margin-top: -17.7%;
      margin-left: -38%;
    }

    &__bottom{
      bottom: 6vh;
      // bottom: 8.8vh;

      &-row{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
      }
    }
    &__btns{
      margin-right: .6vw;

      &-2{
        margin-left: 1.98vw;
      }
    }
    &__title{
      margin-bottom: 7vh;
      font-size: 2.08vw;
    }
    &__txt-1{
      margin-bottom: 2vh;
      width: 12vw;
      font-size: 1.04vw;
    }
    &__dots{
      margin-bottom: -.7vw;
    }
    &__dot{
      font-size: 1.15vw;
      margin-bottom: 1.1vw;

      &>i,
      &::after{
        border-width: 3px;
      }
    }
  }
}
