.aside{
  display: none;
  position: fixed;
  z-index: 110;
  right: 0;
  top: 0;
  width: 90vw;
  height: 100%;
  transform: translateX(120%);
  opacity: 0;
  transition: .5s;
  overflow: auto;

  &.active{
    transform: translateX(0);
    opacity: 1;

    &+.aside__overlay{
      display: block;
    }
  }

  &+.aside__overlay{
    position: fixed;
    z-index: 109;
    display: none;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.3);
  }

  &__wrap{
    position: relative;
    height: 100%;
    padding: 6vmin 8vmin 12vmin;
  }
  &__head{
  }
  &__close{
    display: block;
    order: 5;
    width: 8vmin;
    height: 8vmin;
    bordeR: 0;
    background: none;
    cursor: pointer;
    position: relative;

    &::before,
    &::after{
      content: '';
      display: block;
      position: absolute;
      left: 0;
      width: 100%;
      height: .6vmin;
      top: 50%;
      margin-top: -.3vmin;
      background: var(--color-blue);
      box-shadow: 0px 0px 1vmin rgba(69, 222, 255, 0.7);
      transform: rotate(45deg);
    }
    &::after{
      transform: rotate(-45deg);
    }
  }
  &__footer{
    margin-top: 20vmin;
    margin-bottom: 6vmin;

    .header__btns{
      margin: 0;
      width: 100%;
    }
  }
}


